import Navbar from "@/components/nav/Navbar";
import Image from "next/image";
import Backdrop from "@/assets/backdrop.png";
import {
  Carousel,
  CarouselContent,
  CarouselItem,
  CarouselNext,
  CarouselPrevious,
} from "@/components/ui/carousel"
import Autoplay from "embla-carousel-autoplay"

export default function Home() {
  return (
      <div className="relative min-h-screen">
        <section className="relative w-full h-[80vh] mt-8 overflow-hidden">
        <Image
          src={Backdrop}
          alt="Backdrop"
          fill
          priority
          className="object-cover brightness-75"
        />

        <div className="absolute inset-0 flex items-center justify-center">
        <Carousel
            opts={{ loop: true }}
            plugins={[Autoplay({ delay: 3500 })]} // remove this line if you donâ€™t want autoplay
            className="w-[90%] max-w-xl"
          >
            <CarouselContent>
              {[1, 2, 3, 4, 5].map((i) => (
                <CarouselItem
                  key={i}
                  className="basis-full md:basis-1/2 lg:basis-1/3 p-4"
                >
                  <div className="w-[400px] bg-white/90 rounded-2xl shadow-xl p-8 text-center backdrop-blur-sm">
                    <h3 className="text-lg font-semibold">Slide {i}</h3>
                    <p className="text-sm text-gray-600">
                      Product or content goes here
                    </p>
                  </div>
                </CarouselItem>
              ))}
            </CarouselContent>

            {/* Custom arrow styling */}
            <CarouselPrevious className="bg-gray-900/70 text-white hover:bg-gray-900 absolute left-2" />
            <CarouselNext className="bg-gray-900/70 text-white hover:bg-gray-900 absolute right-2" />
          </Carousel>
        </div>
        </section>
      </div>
  );
}
